<template>
  <div id="header">
    <i v-if="goBack" class="iconLeft" @click="$router.go(-1)"></i>
    <h1>{{headTitle}}</h1>
    <span class="right">{{rightText}}</span>
  </div>
</template>
<script>
  export default{
      name: 'Jheader',
      props: ['headTitle','goBack','rightText']
  }
</script>
<style lang="scss">
  @import "../common/_util.scss";
  @import "../common/_global.scss";

  #header {
    position: fixed;
    top:0;
    left:0;
    width: 100%;
    height:54px;
    background-color: $allColor;
    line-height: 54px;
    z-index: 9;
    h1 {
      color: $bg;
      text-align: center;
      font-size: 20px;
      font-weight: 200;
    }
    .iconLeft {
      position: absolute;
      left:20px;
      color: $bg;
      width: 14px;
      height:20px;
      background: url("../images/back.png") center no-repeat;
      background-size: contain;
      top: 17px;
    }
    .right {
      position: absolute;
      font-size: rem(30);
      color: $bg;
      right: rem(34);
      top:0;
    }
  }
</style>



